.cocktail{
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-end;
    gap: 1rem;

    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    .show{
        display: flex;
    }

    .hidden{
        display: none;
    }

    .imgCocktail{
        position: absolute;
        top: 25%;
        left: 50%;
        transform: translate(-50%);
        width: 16rem;
        margin: auto;
        animation-name: rightToleft;
        animation-duration: 1s;
    }

    @keyframes rightToleft {
        0% {
            position: absolute;
            transform: translate(0);
            overflow-y: hidden;
        }
        100% {
            position: absolute;
            transform: translate(-50%);
            overflow-y: hidden;   
        }
      }
    
    .infoCocktail,
    .recipeCocktail{
        background: rgba(245, 222, 179, 0.93);
        color: black;
        padding: 1rem;
        z-index: 99;
        border-radius: 1rem;
        font-weight: bold;
        color: #682f2f;
        font-size: 1.1rem;
    }

    .infoCocktail{

        .presentationCocktail{
            max-height: 15rem;
            overflow-y: hidden;
            overflow-y: scroll;
            padding: 0 1rem 0 0;
        }

        .presentationCocktail::-webkit-scrollbar{
            width: 0.5rem;
            background-color: transparent;
            border-radius: 10px;
        }
        .presentationCocktail::-webkit-scrollbar-thumb-track{
            display: none;
        }
        .presentationCocktail::-webkit-scrollbar-thumb{
            border-radius: 10px;
            background-color: #682f2f;
        }
    }

    .containerButton{
        display: flex;
        flex-direction: column;
        gap: 1rem;
        background-color: #682f2f;
        border-radius: 50%;

        button{
            font-size: 3rem;
            border: none;
            background-color: transparent;
            color: rgba(245, 222, 179, 0.93);
            cursor: pointer;
            z-index: 99;
        }

        .containerBtn_InfoCocktail,
        .containerBtn_RecipeCocktail{
            display: flex;
            gap: 0.5rem;
            align-items: flex-start;
        }
    }
}

