@media screen and (min-width: 768px) {

    .formSearch{
        width: 50%;
    }

    .cocktail{
        width: 40%;
        margin-top: 2rem;
        flex-direction: row;
        justify-content: flex-end;
        align-items: flex-start;

        .imgCocktail{
            width: 22rem;
            transform: translate(-50%);
            animation-name: rightToleft;
            animation-duration: 1s;
        }
        @keyframes rightToleft {
            0% {
                position: absolute;
                transform: translate(120%);
            }
            100% {
                position: absolute;
                transform: translate(-50%);
                overflow-y: hidden;   
            }
          }

        .infoCocktail,
        .recipeCocktail{
            width: 60%;
            font-size: 1.2rem;

            .presentationCocktail::-webkit-scrollbar{
                width: 0.7rem;
                background-color: transparent;
                border-radius: 10px;
            }
            .presentationCocktail::-webkit-scrollbar-thumb-track{
                display: none;
            }
            .presentationCocktail::-webkit-scrollbar-thumb{
                border-radius: 10px;
                background-color: #682f2f;
            }

        }

        .containerButton{

            button{
                font-size: 4rem;
                border: none;
                background-color: transparent;
                color: rgba(245, 222, 179, 0.93);
                cursor: pointer;
            }

        }

    }
}
